* {
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #0f0d2a;
  line-height: 1.5;
  margin: 0;
  
  
}
p{
  margin: 0;
  padding: 3px;
}


.gallery {
  padding-left: 25px;
  padding-right: 20px;

  display: grid;

    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 15px;

  justify-content: center;
  flex-wrap: wrap;
  

}


.photo-card{
  flex-flow: column;
  flex-wrap: wrap;
  margin-right: 20px;
border: 2px none #9da5aa;
  border-radius: 0px 0px 2px 2px;
box-shadow: 0px 2px 2px 0px rgba(111, 111, 111, 1);
  
}

.gallery__img{
  width: 100%;
    height: 80%;
    object-fit: cover;
}

.info{
  display: flex;
  align-items: center;
  justify-content: center;
  justify-items: center;

 
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;

}

.info-item {
  text-align: center;
  display: flex;
  flex-direction: column ;
  justify-content: center;
}

.search-form {

  position: relative;
    width: 300px;
    margin: 0 auto;
}

.input {
  float: left;
    width: 300px;
    background: #f1f1f1;
    border: none;
    border-top-left-radius: 2px;
    border-bottom-left-radius: 2px;
    outline: none;
    padding: 10px;
    font-size: 15px;
}


.button__submit {
position: absolute;
float: left;
width: 50px;
color: #000;
cursor: pointer;
background: #c3c2c2;
border: none;
border-top-right-radius: 2px;
border-bottom-right-radius: 2px;
padding: 10px;
font-size: 15px;
}
.button__submit:before{
  
content: "\f002";
  font-family: FontAwesome;
  font-size: 16px;
  color: #F9F0DA;
}

.backgroundsearch{
  height: 15%;
  background-color: #4056b4;
  justify-content: center;
    padding: 10px;
    display: flex;
    margin-bottom: 15px;
}


.is-hidden{
  display: none;
  visibility: hidden;
  pointer-events: none;
}


/* CSS */
.load-more {
  display: block;
  outline: 1px solid red;
  margin-left: auto;
  margin-right: auto;
  margin-top: 22px; 
  appearance: none;  
  background-color: #3515c4;
  border: 2px solid #0f0b5a;
  border-radius: 15px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
 
  font-family: Roobert, -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 16px;
  font-weight: 600;
  line-height: normal;

  min-height: 60px;
  min-width: 0;
  outline: none;
  padding: 16px 24px;
  text-align: center;
  text-decoration: none;
  transition: all 300ms cubic-bezier(.23, 1, 0.32, 1); 

  width: 300px;
  

}

.load-more:disabled {
  pointer-events: none;
}

.load-more:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
  transform: translateY(-2px);
}

.load-more:active {
  box-shadow: none;
  transform: translateY(0);
}